





















































































































.search-wrapper {
  background-color: #fff;
  font-size: 28px;
}

.search-input {
  flex-grow: 1;
  margin-right: 20px;
  background: #fff;
  color: #999;
  border-radius: 10px;
  border: 1px solid #e6e6e6;
  height: 60px;
  box-sizing: border-box;
  padding-left: 50px;

  .result-list {
    margin-top: 88px;
  }
}

.movie-box {
  padding-bottom: 20px;
  border-bottom: 20px solid #f5f5f5;
}
.title {
  font-size: 30px;
  color: #999;
  padding: 18px 30px;
}

.movie-section {
  position: relative;
  display: flex;
  align-items: center;
  height: 228px;
  padding: 0 30px;
  border-top: 1px solid #f0f0f0;
  .movie-name {
    font-size: 34px;
    color: #333;
    padding-right: 10px;
  }
  .movie-img {
    width: 128px;
    height: 180px;
  }
}

.movie-info {
  margin: 0 20px;
  font-size: 26px;
  color: #666;
  width: 55%;

  > div {
    margin-bottom: 6px;
  }
}

.version {
  display: inline-block;
  height: 28px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 10px;
  margin-top: 10px;
}

.score {
  flex: 1;
  text-align: right;
  font-size: 18px;
  color: #666;
}

.grade {
  font-size: 32px;
  color: #faaf00;
}

.cinema-section {
  padding: 30px;
  margin: 0;
  border: none;
  border-top: 1px solid #f0f0f0;
}
.van-search__action {
  color: #e54847 !important;
}
